.input_container { position: relative; width: 100%; height: 100%; display: flex; margin-bottom: 6px;} 
.input_container label { position: absolute; z-index: 1; font-size: 14px; margin-top: 22px; transition: all 0.2s; color: slategrey; } 

.input_label_up { margin-top: 0px !important; font-size: 12px !important; color: slategrey; } 
.input_label_active { color: #1967d2; } 

.input_container input { position: relative; width: 100%; height: 100%; border: none; margin-top: 22px; margin-bottom: 6px; font-size: 14px; color: #565656; z-index: 1; background: transparent; } 

.input_line { position: absolute; bottom: -6px; left: 50%; width: 0px; border: none; height: 2px; background-color: #e4e4e4; transition: all 0.3s; z-index: 1; } 
.input_line[name="active_line"] { background-color: #1967d2; z-index: 2; } 

.line_active { width: 100%; left: 0; } 
.line_hide { left: 50%; width: 0px; } 

.error_message { color: #f76464 !important; font-weight: 600; position: absolute; bottom: -20px;}
.error_line { background: #f76464 !important; }